import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import "./Create.css";
import { getGames, getGenre, postGame } from "../../redux/Actions";

const Create = () => {
  const dispatch = useDispatch();
  const allGenres = useSelector((state) => state.allGenres);
  const allGames = useSelector((state) => state.allGames);

  const [input, setInput] = useState({
    name: "",
    image: "",
    description: "",
    platforms: [],
    releaseDate: "",
    rating: "",
    genre: [],
  });
  const [errors, setErrors] = useState({
    name: "Nombre requerido",
    image: "Imagen requerida",
    description: "Descripcin requerida",
    releaseDate: "Fecha de lanzamiento requerida",
    rating: "Rating requerida",
  });
  const resetErrors = () => {
    setErrors({
      name: "Nombre requerido",
      image: "Imagen requerida",
      description: "Descripcin requerida",
      releaseDate: "Fecha de lanzamiento requerida",
      rating: "Rating requerida",
    });
  };

  const validate = (input, name) => {
    const urlRegex = /^(?:\/\/|[^\/]+\/)(?:\S+\/)*\S+\.(?:jpg|jpeg|gif|png)$/i;
    const ratingRegex = /^([0-5](\.\d+)?)$/;

    if (name === "name") {
      if (input.name !== "") setErrors({ ...errors, name: "" });
      else setErrors({ ...errors, name: "Nombre requerido" });
      return;
    }
    if (name === "image") {
      if (input.image !== "") setErrors({ ...errors, image: "" });
      else setErrors({ ...errors, image: "Imagen URL requerida" });
      if (urlRegex.test(input.image)) setErrors({ ...errors, image: "" });
      else setErrors({ ...errors, image: "Formato incorrecto, ingrese URL" });
      return;
    }
    if (name === "description") {
      if (input.description !== "") setErrors({ ...errors, description: "" });
      else setErrors({ ...errors, description: "Descripcin requerida" });
      return;
    }

    if (name === "releaseDate") {
      if (input.releaseDate !== "") setErrors({ ...errors, releaseDate: "" });
      else
        setErrors({
          ...errors,
          releaseDate: "Fecha de lanzamiento requeridas",
        });
      return;
    }
    if (name === "rating") {
      if (input.rating !== "") setErrors({ ...errors, rating: "" });
      else setErrors({ ...errors, rating: "Ingresa Rating de 0-5" });
      if (ratingRegex.test(input.rating)) setErrors({ ...errors, rating: "" });
      else
        setErrors({
          ...errors,
          rating: "Rating solo puede ser de 0 a 5 y decimales.",
        });
      return;
    }
  };

  const disable = () => {
    let disabled = true;
    for (let error in errors) {
      if (errors[error] === "") disabled = false;
      else {
        disabled = true;
        break;
      }
    }
    return disabled;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (input.genre.length === 0) {
      alert("Debe seleccionar al menos un gnero.");
    }
    if (input.platforms.length === 0) {
      alert("Debe seleccionar al menos una plataforma.");
      return;
    }
    dispatch(postGame(input));
    setInput({
      name: "",
      image: "",
      description: "",
      platforms: [],
      releaseDate: "",
      rating: "",
      genre: [],
    });
    resetErrors();
  };

  const handleChange = (e) => {
    setInput({
      ...input,
      [e.target.name]: e.target.value,
    });
    validate(
      {
        ...input,
        [e.target.name]: e.target.value,
      },
      e.target.name
    );
  };

  const handleCheckGenre = (e) => {
    const selected = e.target.value;
    const isChecked = e.target.checked;
    let updatedGenres = input.genre;
    if (isChecked) {
      updatedGenres.push(selected);
    } else {
      updatedGenres = updatedGenres.filter((element) => element !== selected);
    }
    setInput({ ...input, genre: updatedGenres });
  };
  const handleCheckPlatforms = (e) => {
    const selected = e.target.value;
    const isChecked = e.target.checked;
    let updatedPlatforms = input.platforms;
    if (isChecked) {
      updatedPlatforms.push(selected);
    } else {
      updatedPlatforms = updatedPlatforms.filter(
        (element) => element !== selected
      );
    }
    setInput({ ...input, platforms: updatedPlatforms });
  };

  useEffect(() => {
    dispatch(getGenre());
  }, []);

  useEffect(() => {
    dispatch(getGames());
  }, []);

  return (
    <div className="container-form">
      <div className="container-inputs">
        <form autoComplete="off" onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="genre" className="label-nombre">
              Genero
            </label>
            <div className="genre-container">
              {allGenres.map((genre) => (
                <div key={genre.id} className="genre-checkbox-container">
                  <input
                    className="input-checkbox"
                    type="checkbox"
                    name="genre"
                    value={genre.id}
                    onChange={handleCheckGenre}
                    checked={input.genre.includes(genre.id.toString())}
                  />
                  <label className="label-check" htmlFor="genre">
                    {genre.name}
                  </label>
                </div>
              ))}
            </div>
          </div>
          <div className="form-group">
            <label htmlFor="platforms" className="label-nombre">
              Plataformas
            </label>
            <div className="platforms-container">
              {Array.from(
                new Set(allGames.flatMap((game) => game.platforms))
              ).map((platform) => (
                <div className="platforms-checkbox-container" key={platform}>
                  <input
                    className="input-checkbox"
                    type="checkbox"
                    name="platforms"
                    value={platform}
                    onChange={handleCheckPlatforms}
                    checked={input.platforms.includes(platform)}
                  />
                  <label className="label-check" htmlFor={platform}>
                    {platform}
                  </label>
                </div>
              ))}
            </div>
          </div>
          <div className="form-group">
            <label htmlFor="name" className="label-nombre">
              {errors["name"]}
            </label>
            <input
              type="text"
              name="name"
              className="form-control"
              onChange={handleChange}
              placeholder={"Nombre"}
            />
          </div>
          <div className="form-group">
            <label htmlFor="image" className="label-imagen">
              {errors["image"]}
            </label>
            <input
              type="text"
              name="image"
              className="form-control"
              onChange={handleChange}
              placeholder={"Imagen URL"}
            />
          </div>
          <div className="form-group">
            <label htmlFor="description" className="label-description">
              {errors["description"]}
            </label>
            <input
              type="text"
              name="description"
              className="form-control"
              onChange={handleChange}
              placeholder={"descripcion"}
            />
          </div>
          <div className="form-group">
            <label htmlFor="released-date" className="label-fecha">
              Fecha de lanzamiento
            </label>
            <input
              type="date"
              name="releaseDate"
              className="form-control"
              onChange={handleChange}
            />
          </div>
          <div className="form-group">
            <label htmlFor="rating" className="label-rating">
              {errors["rating"]}
            </label>
            <input
              type="text"
              name="rating"
              className="form-control"
              onChange={handleChange}
              placeholder={"Rating"}
            />
          </div>
          <div className="button-container">
            <button disabled={disable()} type="submit" className="btn-submit">
              Submit
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Create;
